import { useContext, useState } from "react";
import { Link, useNavigate } from "react-router-dom"
import clayful from "clayful/client-js"
import { AuthContext } from "../../context/AuthContext";

function LoginPage () {

    const navigate = useNavigate();
    const {isAuthenticated} = useContext(AuthContext);

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    const handleEmailChange = (event) => {
        setEmail(event.target.value);
    }
    const handlePassowrdChange = (event) => {
        setPassword(event.target.value);
    }
    const handleSubmit = (event) => {
        event.preventDefault();
        var Customer = clayful.Customer;

        var payload = {
            email,
            password,
        };

        Customer.authenticate(payload, function(err, result) {

            if (err) {
                // Error case
                // console.log(err.code);
                if (err.code === "invalid-password" || err.code === "not-existing-customer") {
                    alert("   .");
                    return;
                }
            }

            var data = result.data;

            localStorage.setItem("customerUID", data.customer);
            localStorage.setItem("accessToken", data.token);
            navigate("/")
            isAuthenticated();

});
    }

    return (
        <div className="pageWrapper">
            <div className="auth-wrapper">
                <h1></h1>
    
                <form onSubmit={handleSubmit}>
                    <input placeholder="  ." type="email" name="email" value={email} onChange={handleEmailChange} />
    
                    <input placeholder=" ." type="password" name="password" value={password} onChange={handlePassowrdChange} />
    
                    <button type="submit"></button>
    
                    <Link to="/register" style={{ color: 'gray', textDecoration: "none"}}>'s Store  ?  </Link>
                </form>
            </div>
        </div>
    )
}

export default LoginPage