import { useState } from "react"
import {Link, useNavigate} from "react-router-dom"
import clayful from 'clayful/client-js'

function RegisterPage () {

    const navigate = useNavigate();

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    const handleEmailChange = (event) => {
        setEmail(event.target.value);
    }
    const handlePassowrdChange = (event) => {
        setPassword(event.target.value);
        if(event.target.value.length < 8){
            
        }
    }

    const handleSubmit = (event) => {
        event.preventDefault();

        let Customer = clayful.Customer;
    
        let payload = {
            email,
            password,
        };

        console.log(payload);
    
        Customer.createMe(payload, function(err, result) {
    
            if (err) {
                // Error case
                console.log(err.code);
            }
            
            navigate("/login")
        });
    }


    return (
        <div className="pageWrapper">
            <div className="auth-wrapper">
                <h1></h1>
    
                <form onSubmit={handleSubmit}>
                    <input placeholder="  ." type="email" name="email" value={email} onChange={handleEmailChange} />
    
                    <input placeholder=" ." type="password" name="password" value={password} onChange={handlePassowrdChange} />
                    <p></p>
                    <button type="submit"></button>
    
                    <Link to="login" style={{ color: 'gray', textDecoration: "none"}}> 's Store  ?  .</Link>
                </form>
                
            </div>
        </div>
    )
}

export default RegisterPage